import React, { useEffect, useState } from "react";
import {
  Container,
  Button,
  Typography,
  Grid,
  TextField,
  Snackbar,
  SnackbarContent,
  IconButton,
} from "@material-ui/core";

import { red, green } from "@material-ui/core/colors";
import { connect } from "react-redux";
import api from "../api/api";
import { Close } from "@material-ui/icons";


const Contact = ({ profile, auth }) => {
  const [formValues, setFormValues] = useState({
    name: "",
    phone: "",
    email: "",
    address: "",
    message: "",
  });
  const [toast, settoast] = useState(false);
  const [status, setstatus] = useState(null);
  const [message, setmessage] = useState(null);
  useEffect(() => {
    document.title = "Contact Us";
    if (Object.keys(profile).length)
      setFormValues({
        name: profile.name,
        phone: profile.phone,
        email: profile.email,
        address: profile.address,
        message: "",
      });
  }, [auth]);

  const onSubmit = async (e) => {
    e.preventDefault();
    try {
      await api.post(`/api/query`, formValues);
      if (Object.keys(profile).length)
        setFormValues({
          name: profile.name,
          phone: profile.phone,
          email: profile.email,
          address: profile.address,
          message: "",
        });
      else
        setFormValues({
          name: "",
          phone: "",
          email: "",
          address: "",
          message: "",
        });

      setstatus(green[700]);

      setmessage("Query Sent Successful!");
      return settoast(true);
    } catch (err) {
      setstatus(red[700]);

      setmessage("Something went wrong. Try again!");
      return settoast(true);
    }
  };

  return (
    <Container maxWidth="md" >
      <Grid container spacing={5} style={{ marginTop: 50 }}>
        <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
          <Typography variant="h5" gutterBottom style={{ fontWeight: 700, marginBottom: 20 }} color="primary">
            Contact Us
          </Typography>
          <form onSubmit={onSubmit}>
            <TextField
              required
              fullWidth
              variant="outlined"
              margin="normal"
              size="small"
              label="Name"
              value={formValues.name}
              onChange={(e) =>
                setFormValues({ ...formValues, name: e.target.value })
              }
            />
            <TextField
              required
              fullWidth
              variant="outlined"
              margin="normal"
              size="small"
              label="Phone number"
              value={formValues.phone}
              onChange={(e) =>
                setFormValues({ ...formValues, phone: e.target.value })
              }
            />
            <TextField
              required
              fullWidth
              variant="outlined"
              margin="normal"
              size="small"
              type="email"
              label="Email"
              value={formValues.email}
              onChange={(e) =>
                setFormValues({ ...formValues, email: e.target.value })
              }
            />
            <TextField
              required
              fullWidth
              variant="outlined"
              margin="normal"
              size="small"
              label="Address"
              value={formValues.address}
              onChange={(e) =>
                setFormValues({ ...formValues, address: e.target.value })
              }
            />
            <TextField
              required
              fullWidth
              variant="outlined"
              margin="normal"
              multiline
              rows={5}
              size="small"
              label="Message"
              value={formValues.message}
              onChange={(e) =>
                setFormValues({ ...formValues, message: e.target.value })
              }
            />
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              <Button color="primary" variant="contained" type="submit">
                Send
              </Button>
            </div>
          </form>
        </Grid>

        <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
          <Typography variant="h5" gutterBottom>
            Office Location
          </Typography>
          <div style={{ width: "100%" }}>
            {/* <GoogleMapReact
              bootstrapURLKeys={{
                key: "AIzaSyBLdwdo6BIFr8ATGGMLH3kK5Ljh1bkJG0I",
              }}
              defaultCenter={{
                lat: 23.74,
                lng: 92.71,
              }}
              defaultZoom={11}
              yesIWantToUseGoogleMapApiInternals
            >
              <Typography lat={23.74309} lng={92.71685}>
                SMPB
              </Typography>
            </GoogleMapReact> */}
            <img alt="smpb" src="/logo512.png" style={{ width: 200 }} />
          </div>
          <Grid container style={{ marginTop: 20 }}>
            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
              <Typography style={{ fontSize: 16, fontWeight: 500 }}>
                Mission Organic Mizoram, Govt. of
                Mizoram
              </Typography>
              <Typography style={{ color: "#333" }}>
                Tel: 0389-2319025
              </Typography>
              <Typography style={{ color: "#333" }}>
                Email: movcdmiz@gmail.com
              </Typography>
              <Typography style={{ color: "#333" }}>
                Directorate of Agriculture, Tuikual South, Aizawl, Mizoram
              </Typography>
              <Typography style={{ color: "#333" }}>796001</Typography>
            </Grid>

          </Grid>
        </Grid>
      </Grid>

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          settoast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: status }}
          message={
            <span style={{ display: "flex", alignItems: "center" }}>
              {message}
            </span>
          }
          onClose={() => {
            settoast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => settoast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    auth: state.profile,
    profile: state.profile,
  };
};

export default connect(mapStateToProps, null)(Contact);
